#header {
  position: relative;
}

#header.header-another-page .container {
  border-bottom: 1px solid rgba(42, 47, 76, 0.2);
}

#header.header-another-page .container .logo a {
  color: rgba(42, 47, 76, 0.8) !important;
}

#header.header-another-page .container a {
  color: rgba(42, 47, 76, 0.6) !important;
}

#header.header-another-page .container .telephone-contact {
  color: rgba(42, 47, 76, 0.6) !important;
}

#header.header-another-page .container .line {
  stroke: #000;
}

#header .ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#header .hamRotate.active {
  transform: rotate(45deg);
}

#header .hamRotate180.active {
  transform: rotate(180deg);
}

#header .line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #fff;
  stroke-width: 5.5;
  stroke-linecap: round;
}

#header .ham-burger .top {
  stroke-dasharray: 40 160;
}

#header .ham-burger .middle {
  stroke-dasharray: 40 142;
  transform-origin: 50%;
  transition: transform 400ms;
}

#header .ham-burger .bottom {
  stroke-dasharray: 40 85;
  transform-origin: 50%;
  transition: transform 400ms, stroke-dashoffset 400ms;
}

#header .ham-burger.active .top {
  stroke-dashoffset: -64px;
}

#header .ham-burger.active .middle {
  transform: rotate(90deg);
}

#header .ham-burger.active .bottom {
  stroke-dashoffset: -64px;
}

